.control {
  position: relative;
  display: grid;
  grid-template-columns: 41px 1fr;
  gap: 0 29px;
  font-size: 18px;
}
.control__input {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 38px;
  margin: 0;
  grid-column: 1/2;
  grid-row: 1/2;
  opacity: 0;
}
.control--radio .control__input {
  height: 42px;
}
.control__lead {
  grid-column: 1/-1;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 41px 1fr;
  gap: 29px;
  align-items: center;
}
@media (min-width: 660px) {
  .control__lead {
    gap: 20px;
  }
}
.control--checkbox .control__lead {
  gap: 18px;
}
.control__lead::before, .control__lead::after {
  content: "";
  height: 38px;
  grid-column: 1/2;
  grid-row: 1/2;
}
.control--radio .control__lead::before, .control--radio .control__lead::after {
  height: 42px;
}
.control__lead::before {
  background-color: #FFFFFF;
  border-radius: 1px;
  border: 2px solid #E5E5E5;
}
.field-group__item:hover .control__lead::before {
  border-color: #283645;
}
.field-group__item:active .control__lead::before {
  border-color: #D22856;
}
.control--radio .control__lead::before {
  border-radius: 50%;
}
.control--radio .control__input:checked + .control__lead::after {
  background-clip: padding-box;
  background-color: #D22856;
  border: 13px solid transparent;
  border-radius: 50%;
}
@media (min-width: 660px) {
  .control--radio .control__input:checked + .control__lead::after {
    border-width: 11px;
  }
}
.control--checkbox .control__input:checked + .control__lead::after {
  background-repeat: no-repeat;
  background-position: calc(50% + 1px) 50%;
  background-image: url("../img/icons/check.svg");
}

